.sidebar-nav-main {
  width: 78px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  background-color: #06bcfb;
  background-image: linear-gradient(315deg, #06bcfb 0%, #4884ee 74%);
  padding: 6px 7px;
  transition: all 0.5s ease;
}

.sidebar-nav-main .logo-content .logo {
  color: #fff;
  height: 50px;
  width: 100%;
  padding-top: 15px;
  align-items: center;
  pointer-events: none;
  opacity: 0;
  transition: all 0.3s ease;
}

.sidebar-nav-main .logo-content .logo i {
  font-size: 34px;
  margin-right: 5px;
}

.sidebar-nav-main .logo-content .logo .logo-name {
  font-size: 20px;
  font-weight: 400;
}

.sidebar-nav-main #btn {
  position: absolute;
  left: 50%;
  top: 6px;
  height: 50px;
  width: 50px;
  font-size: 20px;
  line-height: 50px;
  text-align: center;
  color: #fff;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  cursor: pointer;
}

.sidebar-nav-main ul {
  margin-top: 20px;
  margin-left: -30px;
}

.sidebar-nav-main ul li {
  position: relative;
  height: 50px;
  width: 100%;
  margin: 0 5px;
  list-style: none;
  line-height: 50px;
}

.sidebar-nav-main ul li .alink {
  white-space: nowrap;
  width: 50px;
}

.sidebar-nav-main ul li .tooltip {
  position: absolute;
  top: 0;
  left: 117px;
  border-radius: 6px;
  height: 35px;
  width: 122px;
  line-height: 35px;
  text-align: center;
  background: #fff;
  font-weight: bolder;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  transition: 1s;
  opacity: 0;
  pointer-events: none;
  display: block;
}

.sidebar-nav-main ul li .links-name {
  opacity: 0;
  pointer-events: none;
  transition: all 0.2s ease;
}

.sidebar-nav-main ul li input {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  border-radius: 12px;
  outline: none;
  border: none;
  background: #1d1b31;
  padding-left: 50px;
  font-size: 18px;
  color: #fff;
}

.sidebar-nav-main ul li .bx-search {
  position: absolute;
  z-index: 99;
  color: #fff;
  font-size: 22px;
  transition: all 0.5s ease;
}

.sidebar-nav-main ul li .alink {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  transition: all 0.2s ease;
  border-radius: 12px;
  white-space: nowrap;
  transition: all 0.4s ease;
}

.sidebar-nav-main ul li .alink:hover {
  color: #ffffff;
  background: rgba(255, 255, 255, 0.164);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.sidebar-nav-main ul li i {
  height: 50px;
  min-width: 50px;
  border-radius: 12px;
  line-height: 50px;
  text-align: center;
}

.sidebar-nav-main ul li:hover .tooltip {
  top: 50%;
  opacity: 1;
  transition: all 0.5s ease;
}

.sidebar-nav-main .profile-content {
  position: absolute;
  color: #fff;
  bottom: 0;
  left: 0;
  width: 100%;
}

.sidebar-nav-main .profile-content .profile {
  position: relative;
  padding: 10px 6px;
  height: 70px;
  background-color: none;
  transition: all 0.8s ease;
}

.sidebar-nav-main .profile-content .profile .profile-details {
  display: flex;
  align-items: center;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  transition-delay: 0.2s;
}

.sidebar-nav-main .profile-content .profile .profile-details img {
  height: 45px;
  width: 45px;
  object-fit: cover;
  border-radius: 12px;
}

.sidebar-nav-main .profile-content .profile .profile-details .name-job {
  margin-left: 10px;
}

.sidebar-nav-main .profile-content .profile .profile-details .name {
  font-size: 15px;
  font-weight: 400;
}

.sidebar-nav-main .profile-content .profile .profile-details .job {
  font-size: 12px;
}

.sidebar-nav-main .profile-content .profile #log-out {
  min-width: 50px;
  line-height: 50px;
  position: absolute;
  left: 50%;
  background: linear-gradient(rgba(0, 255, 255, 0.76), rgba(0, 0, 255, 0.589));
  bottom: 5px;
  font-size: 20px;
  border-radius: 12px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  text-align: center;
  cursor: pointer;
  transition: all 0.5s ease;
}

.sidebar-nav-main .profile-content .profile #log-out:hover {
  background: linear-gradient(rgba(0, 0, 255, 0.589), rgba(0, 255, 255, 0.76));
}

.nav-list-section .alink.active {
  background-color: rgba(255, 255, 255, 0.233);
}

.sidebar-nav-main.active-nav {
  width: 240px;
  transition-delay: 0.05ms;
}

.sidebar-nav-main.active-nav .logo-content .logo {
  opacity: 1;
}

.sidebar-nav-main.active-nav .profile {
